/* Modern Exam System App Theme & Utility Classes */
:root {
  --accent: #3b82f6;
  --text-primary: #1f2937;
  --text-secondary: #6b7280;
  --text-muted: #9ca3af;
  --border-light: #e5e7eb;
  --border-focus: var(--accent);
  --bg-white: #fff;
  --bg-gray-50: #f9fafb;
  --bg-gray-100: #f3f4f6;
  --shadow-sm: 0 1px 2px rgba(0,0,0,0.05);
  --shadow-md: 0 4px 6px rgba(0,0,0,0.1);
  --radius-sm: 4px;
  --radius-md: 6px;
  --radius-lg: 8px;
  --radius-xl: 12px;
}

body {
  background: var(--bg-gray-50);
  margin: 0;
}

.form-container {
  max-width: 540px;
  margin: 2rem auto;
  padding: 2.3rem;
  box-shadow: var(--shadow-md);
  border-radius: var(--radius-lg);
  background: var(--bg-white);
}

label {
  display: block;
  font-size: 0.97rem;
  font-weight: 500;
  color: var(--text-secondary);
  margin-bottom: 0.22rem;
  letter-spacing: 0.01em;
}

input, textarea, select {
  width: 100%;
  height: 2.5rem;
  padding: 0.5rem;
  border: 1px solid var(--border-light);
  border-radius: var(--radius-md);
  font-size: 1rem;
  transition: border .2s, box-shadow .2s;
  margin-bottom: 1rem;
}
input:focus, textarea:focus, select:focus {
  outline: none;
  border-color: var(--border-focus);
  box-shadow: 0 0 0 3px rgba(59,130,246,0.08);
}
textarea {
  min-height: 4rem;
}

.btn-primary {
  background: var(--accent);
  color: #fff;
  border: 1px solid var(--accent);
  padding: 0.5rem 1.05rem;
  border-radius: var(--radius-md);
  font-weight: 550;
  cursor: pointer;
  transition: background .19s, transform .13s;
  margin-right: 0.6rem;
}
.btn-primary:hover {
  background: #116cf6;
}
.btn-secondary {
  background: transparent;
  color: var(--accent);
  border: 1px solid var(--accent);
  border-radius: var(--radius-md);
  padding: 0.5rem 0.9rem;
  font-weight: 520;
  cursor: pointer;
  transition: background .18s;
}
.btn-secondary:hover {
  background: var(--accent);
  color: #fff;
}

.error {
  border-left: 3px solid #ef4444;
  color: #dc2626;
  padding-left: 0.45rem;
  margin-bottom: 0.6rem;
  background: #fff5f6;
  border-radius: var(--radius-sm);
  font-size: 0.99rem;
}

:disabled {
  opacity: 0.62;
  cursor: not-allowed;
  pointer-events: none;
}

.empty-state {
  text-align: center;
  color: var(--text-muted);
  font-style: italic;
  padding: 2rem;
}

.status-active {
  background: #e1fae1;
}
.status-pending {
  background: #fff5f1;
}

/* Question nav and answers */
input[type="radio"] {
  width: auto;
  margin-right: 0.7rem;
}

h2, h3 {
  color: var(--text-primary);
  margin-top: 0;
}

/* Remove old create-react-app junk */
.App, .App-header, .App-logo, .App-link {
  display: none;
}
